
<template>
  <div class="home">
    <!-- 中间地图 -->
    <div class="news_map">
      <singleMap />

    </div>
    
    <!-- 头部专题切换 -->
    <div class="home_header">
      <el-button type="primary" class="cqupt item" @click="cqupt">重邮版</el-button>
      <!-- <router-link to="/cqupt" class="cqupt item">重邮版</router-link> -->
      <div class="top_bar">
       
        <div class="center_content">
          
          <div class="topleft">
            <el-button size="medium" class="subject item" @click="change(1)"
              >学科建设</el-button
            >
            <el-button class="enroll item" @click="change(2)"
              >招生就业</el-button
            >
            <el-button class="teach item" @click="change(3)"
              >师风师德</el-button
            >
          </div>
          <el-button class="all" @click="change(9)">热点舆情总览</el-button>
          <div class="topright">
            <el-button class="school_construction item" @click="change(4)"
              >校园基建</el-button
            >
            <el-button class="bad item" @click="change(5)">学术不端</el-button>
            <el-button class="epidemic_health item" @click="change(6)"
              >疫情专题</el-button
            >
          </div>
        </div>
        
      </div>
    </div>

    <div class="home_content">
      <!-- 左侧新闻概览展示 -->
      <div class="content_left">
        <div class="news_index">
          <newsindex ref="sonMethod" />
        </div>
      </div>
      <!-- 总数概览 -->
      <div class="content_center">
        <div class="news_num">
          <newsNum ref = "sonNums"/>
        </div>
      </div>

      <div class="contetn_right">
        <!-- 警报 -->
        <div class="warn">
         
          <trend />
        </div>

        <!-- 情感柱状 -->
        <div class="text_analyse">
          <emotion />
        </div>
      </div>
    </div>
    <div class="change_page">
      <changePage />
    </div>
  </div>
</template>

<script>
import topBar from "@/components/navigation/TopBar";
import singleMap from "@/components/content/all/Map.vue";
import trend from "@/components/content/all/Trend.vue";
import newsindex from "@/components/content/all/NewsIndex.vue";
import emotion from "@/components/content/all/Emotion.vue";
import newsNum from "@/components/content/all/NewsNum";
import changePage from "@/components/navigation/AllBar";

import wordCloud from "@/components/content/all/wordCloud.vue";

export default {
  data() {
    return {
      text: "",
      isShow: true,
      flag: "all",
      input: "",
      weibo_id:'',
      
    };
  },
  name: "Home",
  components: {
    topBar,
    singleMap,
    trend,
    newsindex,
    emotion,
    newsNum,
    changePage,
    wordCloud
  },
  mounted() {
    this.all()
  },
  methods: {
    
    all() {
      this.flag = "all";
      //hot3新闻
      this.$url
        .get("news/hot3/")
        .then((res) => {
          let screenData = res.data;
          this.allData = res.data;
          this.$store.commit("setScreenData", screenData);
          this.$refs.sonMethod.onClick();
        })
        .catch((err) => {
          // console.log(err);
        });
      
    },
    cqupt () {
      this.$router.push('/Cqupt')
    },
    change(e) {
      let keyWord;
      switch (e) {
        case 9:
          keyWord = 9;
          break;
        case 1:
          keyWord = "学科建设";
          break;
        case 2:
          keyWord = "招生就业";
          break;
        case 3:
          keyWord = "师风师德";
          break;
        case 4:
          keyWord = "校园基建";
          break;
        case 5:
          keyWord = "学术不端";
          break;
        case 6:
          keyWord = "疫情专题";
          break;
        default:
          break;
      }
        let param;
        if (keyWord == 9) {
          param = "news/hot3/";
        } else {
          param = "news/hot3/?aspect=" + keyWord;
        }
        this.$url
          .get(param)
          .then((res) => {
            let screenData = res.data;
            this.allData = res.data;
            this.$store.commit("setScreenData", screenData);
            this.$refs.sonMethod.onClick();
          })
          .catch((err) => {
            // console.log(err);
          });
    },
   

  },
  
};
</script>

<style scoped lang="less">
