  position: relative;
  margin: 120px auto;
  width: 50%;
  height: 90%;
  // background-color: #e9f2f3;
}
.topleft {
  display: flex;
  flex-direction: row;
}
.topright {
  display: flex;
  flex-direction: row;
}
.item {
  position: relative;
  z-index: inherit;
  width: 100px;
  height: 30px;
}
.all {
  position: relative;
  z-index: inherit;
}
.el-button {
  color: #10e7f8;
  font-size: 18px;
  font-weight: bold;
  background-color: transparent;
  border: 1px solid transparent;
}

.el-button:hover,
.el-button:focus {
  background-color: transparent;
  color: #d5d7dc;
  border: 1px solid transparent;
}

.home {
  width: 100vw;
  // height: 100vh;
  background-image: url("../assets/screen/bg2.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  .el-input__inner{
        border-radius:20px;
        color: #24E0D2;
        border: solid 1px #24E0D2;
        height: 32px;
        background-color: transparent;
      }
     input::-webkit-input-placeholder {
      color: #24E0D2;
      }
  .home_content {
    width: 100%;
    display: flex;
    justify-content: space-around;
    .content_left {
      width: 40%;
      height: 100vh;
      .news_index {
        margin-top: 30px;
        margin-left: 20px;
      }
    }
    .content_center {
      width: 45%;

      .news_num {
        width: 40%;
        margin-top: 25px;
        z-index: 1000;
      }
    }
    .contetn_right {
      width: 35%;
      margin-left: -30px;
      display: flex;
      flex-direction: column;
      margin-right: 60px;
      .warn {
        width: 90%;
        height: 30%;
        padding: 10px;
         background: url('../assets/screen/border.png') no-repeat;
         background-size: 100% 100%;
        cursor: pointer;
      }
      .warnImg {
        position: absolute;
        right: 150px;
        color: white;
        font-size: 20px;
        img {
          margin-top: 30px;
          width: 100px;
        }
        .content {
          width: 300px;
          font-size: 13px;
