        }
      }
      .text_analyse {
        background: url("../assets/screen/emotion.png") no-repeat;
        background-size: 100% 100%;
        margin-top: -25px;
        width: 100%;
        height: 400px;
      }
    }
  }
}
.change_page {
  width: 100%;
  position: absolute;
  bottom: -180px;
  transition-property: bottom;
  transition-duration: 1s;
  z-index: 999;
}
.change_page:hover {
  bottom: 20px;
}
.news_map {
 
  width: 100%;
