<template>
  <div>
      <div class="now_news_num">
          <div class="news_count">当前舆情总量</div>
          <div class="no">
              <div class="no-hd">
                  <ul >
                      <li>{{allnums}}</li>
                      <li>{{allnums}}</li>
                  </ul>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      allnums: ''
    }
  },
  mounted () {
    this.getData()
    // console.log(this.$store.state.allNums)
  },
  methods : {
    onClick(param) {
      this.allnums = this.$store.state.allNums
      // console.log('取的',this.allnums)
    },
    async getData () {
      const {data:ret} = await this.$url ('news/nums/')
      this.allnums = ret.nums;
      // console.log('舆情总量',this.allnums)

    }
  }

}
</script>

<style lang="less" >
@font-face{
    font-family: electronicFont;
    src:url('../../../assets/font/DS-DIGIT.TTF')
}
.now_news_num {
   background:url('../../../assets/screen/number.png')no-repeat;
   background-size: 100% 100%;
   width: 320px;
   margin-top: -20px;
   margin-left:-60px;
   padding: 20px;
   display: flex;
   flex-direction: column;
   height: 180px;

   .news_count{
    width:100%;
    height: 20%;
    // text-align: center;
    margin-left: 20px;
    color:#98E3F1;
    font-size: 22px;
    line-height: 1.2;
    letter-spacing:.1em;
    padding:10px;

  }
  .no{
    width: 80%;
    height: 80%;
    margin-left: 10%;
    .no-hd{
      position: relative;
      ul{
        display: flex;
        margin:5% 0;
        align-items: center;
        li{
          flex: 1;
          line-height: 1.5;
          font-size: 40px;
          color: #ffeb7b;
          text-align: center;
          font-family: 'electronicFont';
          position: relative;
          margin-right: 8px;
        
        }
        li:nth-child(1) {
            &::after{
            content: "";
            position: absolute;
            top: 15%;
            height: 70%;
            width: 2px;
            right: -5px;
            background-color: rgba(255, 255, 255, 0.692);
          }
        }
        li:nth-child(2) {
          font-size: 55px;
        }
      }
    }
  }
}
</style>