<template>
  <div class="new-container" >
    <div class="news_index_header">
      <div class="viewpaper">
        <div class="news_index_title">热度新闻指数</div>
        <div class="marque" id="list">
          <div class="row-title">
            <span class="col">学校名称</span>
            <span class="col">舆情内容</span>
            <span class="col">新闻指数</span>
          </div>
         
          <li  class="row " v-for="(item,index) of allData" :key ="index"
          :class="{active: isActive == index}"
          @mouseover="changeClass(index)" @click="toReport(index)">
            <span class="col">{{item.college}}</span>
            <span class="col" >{{item.raw_text.substring(0,13)}} ...</span>
            <span class="col">{{item.hot_value}}</span>
          </li>
        </div>
      </div>
    </div>
    <div class="news_index_footer">
      <div class="viewpaper">
        <div class="news_index_title">热度评论与走势</div>
        <div class="marque">
          <div class="row-title">
            <span class="col">热点评论</span>
            <span class="col">评论者ID</span>
          </div>
           <li  class="row " v-for="(item,index) of commentForm" :key ="index">
            <span class="col">{{item.comment_content.substring(0,15)}}...</span>
            <span class="col" >{{item.comment_id}}</span>
          </li>
         
         
        </div>
        <div class="com-chart" ref="trend_ref">
          <!-- echart图 -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  data() {
    return {
      myChart: null,
      allData:[
        {college:'',raw_text:'',hot_value:''},
        {college:'',raw_text:'',hot_value:''},
        {college:'',raw_text:'',hot_value:''},
      ],
      isActive:0,
      // seriesArr:[],
      // historyWeibo=[],
      commentForm:[{comment_content:'',comment_id:''},{comment_content:'',comment_id:''}],
      oldNews:[],
      converData:[],
      news:[]
    };
  },
  mounted() {
   this.getData ()

  },
  updated () {
    
  },
  
  methods: {
    onClick(param){     
        this.news = this.$store.state.screenData.data
        this.allData = this.news 
        // console.log('vuex',this.news[0].raw_text)//这里不打印
        this.commentForm = this.news[this.isActive].hot_comments;
        this.oldNews = this.news[this.isActive].history_weibo;
        let hotValue =[];
        this.oldNews.map(item => {
          hotValue.push(
           item.hot_value
          )
        })
        this.converData= (hotValue.slice(0,6)).reverse();
        // console.log(this.isActive);
        this.drawLine();
              
    },
    toReport (i) {
      let newData = this.allData[i]
      // console.log('hot3',newData)
      let author = {
        author_id:newData.author_id,
        author_id_id:newData.author_id_id,
        author_avatar:newData.author_avatar,
        raw_text:newData.raw_text,
        author_gender:newData.author_gender,
        weibo_id:newData.weibo_id,
        f:newData.gender.f,
        m:newData.gender.m,
        hot_comments:newData.hot_comments,
        history_weibo:newData.history_weibo,
        comment_word_cloud:newData.comment_word_cloud,
        neg:newData.comment_emotion_rate.neg,
        other:newData.comment_emotion_rate.other,
      }
      this.$store.commit('setNickName',author);
      this.$store.commit('setAuthor',author);
      this.$store.commit('setAvatar',author);
      this.$store.commit('setText',author);
      this.$store.commit('setGender',author);
      this.$store.commit('setHistoryWeibo',author);
      this.$store.commit('setGender',author);
      this.$store.commit('setCommit',author);
      this.$store.commit('setCommentCloud',author);
      this.$store.commit('setCommentEmotion',author)
      this.$store.commit('setWebId',author);
      // console.log('hot3存的',author)
      this.$router.push('/manage/report')
    },
    getData (e) {
       this.$url
      .get('news/hot3/')
      .then(res => {
        let screenData = res.data;
        this.$store.commit('setScreenData',screenData);
        this.news = this.$store.state.screenData.data
        this.allData = this.news 
        this.commentForm = this.news[this.isActive].hot_comments;
        this.oldNews = this.news[this.isActive].history_weibo;
        let hotValue =[];
        this.oldNews.map(item => {
          hotValue.push(
           item.hot_value
          )
        })
        this.converData= (hotValue.slice(0,6)).reverse();      
        
        this.drawLine();
      })
      .catch(err => {
        // console.log(err)
      });
    
    this.changeClass(0);
    },
   
    drawLine() {
      this.myChart = this.$echarts.init(document.querySelector(".com-chart"));
      const option = {
        title: {
                  show: true,
                  text: '博主历史新闻热度',
                  textStyle: {
                      align: 'center',
                      color: '#98E3F1',
                      top:5,
                      // color: '#A9AFC7',
                      fontSize: 16,
                      "fontWeight": 'normal'

                  },
                  top: '0',
                  left: '0',
        },
        tooltip: {
            trigger: 'axis',
            axisPointer: {
                lineStyle: {
                    color: {
                        type: 'linear',
                        x: 0,
                        y: 0,
                        x2: 0,
                        y2: 1,
                        colorStops: [{
                            offset: 0,
                            color: 'rgba(0, 255, 233,0)'
                        }, {
                            offset: 0.5,
                            color: 'rgba(255, 255, 255,1)',
                        }, {
                            offset: 1,
                            color: 'rgba(0, 255, 233,0)'
                        }],
                        global: false
                    }
                },
            },
        },
        grid: {
            top: '20%',
            left: '1%',
            right: '10%',
            bottom: '42%',
            containLabel: true,
        },
        xAxis: [{
            type: 'category',
            axisLine: {
                show: true
            },
            splitArea: {
                // show: true,
                color: '#f00',
                lineStyle: {
                    color: '#f00'
                },
            },
            axisLabel: {
                color: '#A9AFC7'
            },
            splitLine: {
                // show: falses
                show: true,
                lineStyle: {
                color: ['#fff'],
                opacity: 0.06
                }
            },
            boundaryGap: false,
            data: ["-5","-4","-3","-2","-1","预测"],

        }],
        yAxis: [{
            type: 'value',
            min: 0,
            splitNumber: 4,
            splitLine: {
                show: true,
                lineStyle: {
                color: ['#fff'],
                opacity: 0.06
                }
            },
            axisLine: {
                show: true,
            },
            
            axisLabel: {
                show: true,
                margin: 20,
                textStyle: {
                fontSize: 10,
                color: '#A9AFC7'
                }
            },
            axisTick: {
                show: true,
            },
        }],
        series: [
          {
                type: 'line',
                smooth: false, //是否平滑
                showAllSymbol: true,
                symbol: 'circle',
                symbolSize: 10,
                lineStyle: {
                    normal: {
                        color: "#2CABE3",
                    },
                },
                label: {
                    show: true,
                    position: 'top',
                    textStyle: {
                        color: '#2CABE3',
                    }
                },
                itemStyle: {
                    color: "#fff",
                    borderColor: "#2CABE3",
                    borderWidth: 2,
                },
                tooltip: {
                    show: false
                },
                  areaStyle: { //区域填充样式
                        normal: {
                        //线性渐变，前4个参数分别是x0,y0,x2,y2(范围0~1);相当于图形包围盒中的百分比。如果最后一个参数是‘true’，则该四个值是绝对像素位置。
                        color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                            { offset: 0,  color: 'rgba(61,234,255, 0.9)'}, 
                            { offset: 1,  color: 'rgba(61,234,255, 0)'}
                        ], false),

                        shadowColor: 'rgba(53,142,215, 0.9)', //阴影颜色
                        shadowBlur: 20 //shadowBlur设图形阴影的模糊大小。配合shadowColor,shadowOffsetX/Y, 设置图形的阴影效果。
                    }
                },
                data:this.converData
          }
        ]
      };
      this.myChart.setOption(option);      
    },
    changeClass(i) {
        this.isActive =i;
        // console.log('hover',this.isActive)
        this.commentForm = this.allData[this.isActive].hot_comments;
        // console.log('联动评论',this.commentForm)
        this.oldNews = this.allData[this.isActive].history_weibo;
        let hotValue =[];
        this.oldNews.map(item => {
          hotValue.push(
           item.hot_value
          )
        })
        this.converData= (hotValue.slice(0,6)).reverse();
      this.updataOption()
    },
    updataOption () {
      const dataOption = {
        series: [
          {
                type: 'line',
                smooth: false, //是否平滑
                showAllSymbol: true,
                symbol: 'circle',
                symbolSize: 10,
                lineStyle: {
                    normal: {
                        color: "#2CABE3",
                    },
                },
                label: {
                    show: true,
                    position: 'top',
                    textStyle: {
                        color: '#2CABE3',
                    }
                },
                itemStyle: {
                    color: "#fff",
                    borderColor: "#2CABE3",
                    borderWidth: 2,
                },
                tooltip: {
                    show: false
                },
                  areaStyle: { //区域填充样式
                        normal: {
                        //线性渐变，前4个参数分别是x0,y0,x2,y2(范围0~1);相当于图形包围盒中的百分比。如果最后一个参数是‘true’，则该四个值是绝对像素位置。
                        color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                            { offset: 0,  color: 'rgba(61,234,255, 0.9)'}, 
                            { offset: 1,  color: 'rgba(61,234,255, 0)'}
                        ], false),

                        shadowColor: 'rgba(53,142,215, 0.9)', //阴影颜色
                        shadowBlur: 20 //shadowBlur设图形阴影的模糊大小。配合shadowColor,shadowOffsetX/Y, 设置图形的阴影效果。
                    }
                },
                data:this.converData
          }
        ]
      }
      this.myChart.setOption(dataOption);      

    },
    resizeChart () {
        this.myChart ? this.myChart.resize() : ''
    }
  },
};
</script>

<style lang="less" scoped>
.new-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  margin-left:30px;
  
  .news_index_header {
    background: url('../../../assets/screen/hot-new.png') no-repeat;
    background-size: 100% 100%;
    margin-top:-15px;
    padding: 30px;
    height: 25%;
    display: flex;
    justify-content: center;
    .news_index_title {
      font-size: 22px;
      color:#98E3F1;
      margin-left: 38px;
      letter-spacing:.1em;
    }
    .viewpaper {
      position: relative;
      width: 100%;
      height: 80%;
      padding-top:0;
      
      display: flex;
      flex-direction: column;
      

      .marque {
        width: 100%;
        margin-top: 20px;
        position: absolute;
        text-align: center;
        height: 150px;
        margin-right: 10px;
        animation: move 10s linear infinite;
        .row-title {
          display: flex;
          justify-content: space-between;
          line-height: 1;
          font-size: 18px;
          color: #98E3F1;
          padding: 25px 0px 10px 0px;
          width: 90%;
          margin-left: 50%;
          transform: translate(-50%, -0%);
          margin-top: 5px;
        }
        .row {
          display: flex;
          justify-content: space-between;
          line-height: 1.2;
          font-size: 16px;
          color: #61a8ff;
          padding: 6px 0px;
          width: 95%;
          margin-left: 50%;
          transform: translate(-50%, -0%);
        }
        .active {
          
          color:#fff;
        }
        .row:hover {
          background-color: rgba(255, 255, 255, 0.1);
          color: #68d8fe;
          cursor: pointer;
        }
      }
    }
  }
  .news_index_footer {
    width: 100%;
    height:450px ;
    margin-top: -40px;
    background: url('../../../assets/screen/trend.png') no-repeat;
    background-size: 100% 100%;
    .viewpaper {
      width: 100%;
      padding: 30px;
      height: 100%;
      display: flex;
      flex-direction: column;
      .news_index_title {
        height: 5%;
        font-size: 22px;
        color:#98E3F1;
        padding-top: 15px;
        margin-left: 38px;
        letter-spacing:.1em;
      }
      .marque {
        width: 80%;
        height:30%;
        padding :5px;
        .row-title {
          text-align: center;
          display: flex;
          justify-content: space-between;
          line-height: 1;
          font-size: 18px;
          color: #98E3F1;
          padding: 20px 0px 10px 0px;
          width: 90%;
          margin-left: 50%;
          transform: translate(-50%, -0%);
          margin-top: 2px;
        }

        .row {
          display: flex;
          justify-content: space-between;
          line-height: 1;
          font-size: 14px;
          color: #61a8ff;
          padding: 5px 0px;
          width: 90%;
          margin-left: 50%;
          transform: translate(-50%, -0%);
        }
      }
      .com-chart {
        width: 80%;
        height: 80%;
      }
    }
  }
}
</style>
