<!-- 情感分析柱状图 -->
<template>
  <div class="bar-container">
         <div class="title">各高校新闻情感分析</div>
         <div class="com-chart4"></div>
  </div>
</template>

<script>
export default {
    name: 'emotion',
    data () {
        return {
        myChart:null,
        allData:null,
        startValue:0,//区域缩放起点值
        endValue:3,//区域缩放终点值
        timeId:null
        }
    },
    mounted(){
        this.drawLine();
        this.getData();
        window.addEventListener('resize',this.resizeChart)
    },
    destroyed() {
        window.removeEventListener('resize',this.resizeChart)
        clearInterval(this.timeId)
    },
    methods: {
        drawLine(){
            // 基于准备好的dom，初始化echarts实例
            this.myChart = this.$echarts.init(document.querySelector('.com-chart4'))
            
            // 绘制图表
            const initoption = {
                "tooltip": {
                    "trigger": "axis",
                    "axisPointer": {
                        "type": "shadow",
                        textStyle: {
                            color: "#fff"
                        }

                    },
                },
                "grid": {
                    "borderWidth": 0,
                    "top": 14,
                    "bottom":140,
                    textStyle: {
                        color: "#fff"
                    }
                },
                "calculable": true,
                "xAxis": [{
                    "type": "category",
                    "axisLine": {
                        lineStyle: {
                            color: 'rgba(255,255,255,.5)'
                        }
                    },
                    "splitLine": {
                        "show": false
                    },
                    "axisTick": {
                        "show": false
                    },
                    "splitArea": {
                        "show": false
                    },
                    "axisLabel": {
                        "interval": 0,
                        color:'#fff',
                        fontSize:12,
                    },
                   
                }],
                "yAxis": [{
                    "type": "value",
                    "splitLine": {
                        "show": false
                    },
                    "axisLine": {
                        "show": false
                    },
                    "axisTick": {
                        "show": false
                    },
                    "axisLabel": {
                        "interval": 0,
                        color:'#fff',
                        fontSize:8

                    },
                    "splitArea": {
                        "show": false
                    },

                }],
                "series": [
                    
                ]
            }
            this.myChart.setOption(initoption)
            this.myChart.on('mouseover',() => {
                clearInterval(this.timeId)
            })
            this.myChart.on('mouseon', () => {
                this.startInterval()
            })
        },
        async getData () {
            const {data:ret}= await this.$url.get('news/emotionProportion/')
            this.allData= ret.data
           
            // console.log(typeof this.allData)
            //实现从大到小排序
            this.allData.sort((a,b) => {
                return b.neg-a.neg
            })
            
            this.updateChart()
            //启动定时器
            this.startInterval()
        },
        updateChart () {
            //所有学校形成的数组
            const collegeArr = this.allData.map(item => {
                return item.college
            })
            //所有学校对应的负面数组
            const negArr = this.allData.map(item => {
                return item.neg
            })
            const posArr = this.allData.map(item => {
                return item.other
            })
            const totalArr = this.allData.map(item => {
                return item.neg+item.other
            })
            const dataOption = {
                xAxis : {
                    data:collegeArr
                },
                //区域缩放
                dataZoom:{
                    show:false,
                    startValue:this.startValue,
                    endValue:this.endValue
                },
                series:[
                   //负面填充
                    {
                        "name": "负面",
                        "type": "bar",
                        "stack": "总量",
                        "barMaxWidth": 20,
                        "barGap": "10%",
                        "label": {
                            "show":true,
                        },
                        "itemStyle": {
                            "normal": {
                                // "color": "#2DB1EF",
                                "color": 
                                {
                                    type: 'linear',
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    colorStops: [{
                                        offset: 0,
                                        color: 'rgba(35, 157, 250, 1)' // 0% 处的颜色
                                    }, {
                                        offset: 1,
                                        color: 'rgba(35, 157, 250, 0)' // 100% 处的颜色
                                    }],
                                    global: false // 缺省为 false
                                }
                            }
                        },
                        "data":negArr
                        
                    },
                    //正面填充
                    {
                        "name": "正面",
                        "type": "bar",
                        "stack": "总量",
                        "itemStyle": {
                            "normal": {
                                "color":'rgb(35,250,187)',
                                "barBorderRadius": 0
                            }
                        },
                        "data":posArr
                       
                    }, 
                    //总数填充
                    {
                        "name": "总数",
                        "type": "line",
                        symbolSize: 7,
                        symbol: 'circle',
                        "label": {
                            "show":true,
                        },
                        "itemStyle": {
                            "normal": {
                                "color": 'rgba(255, 196, 53, 1)',
                                "barBorderRadius": 0,
                            }
                        },
                        lineStyle:{
                            normal:{
                                width:4,
                                color:{
                                    type: 'linear',
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    colorStops: [{
                                        offset: 0,
                                        color: 'rgba(255, 67, 2, 1)' // 0% 处的颜色
                                    }, {
                                        offset: 1,
                                        color: 'rgba(255, 196, 53, 1)' // 100% 处的颜色
                                    }],
                                    global: false // 缺省为 false
                                }
                            }
                        },
                        "data":totalArr
                       
                    },
                ]
            }
            this.myChart.setOption(dataOption)

        },
        startInterval (){
            if(this.timeId) {
                clearInterval(this.timeId)
            }
            this.timeId = setInterval(()=> {
                this.startValue++;
                this.endValue++;
                if(this.endValue > this.allData.length-1){
                    this.startValue = 0
                    this.endValue = 3
                }
                this.updateChart()
            },2000)

        },
        resizeChart () {
            this.myChart ? this.myChart.resize() : ''
        }
    }
}


</script>

<style lang="less" >
.bar-container {
    width: 100%;
    height: 90%;
    padding-top:20px;
    .title{
     font-size: 22px;
      color:#98E3F1;
      padding-top: 20px;
      margin-left:180px;
      letter-spacing:.1em;
    
}
.com-chart4{
    width: 100%;
    height: 94%;
    padding-left:20px;
    padding-top:10px;
    position: relative;
    left:0;
}
}

</style>